.adminPages{
    margin: 20px;
    padding-bottom: 100px;
}
.adminForm{
    max-width: 800px;
    text-align: left;
    padding:8px;
    background: #fefefe;
    margin: 0 auto;
    font-weight: bold;
    font-size: 14px;
}
.adminForm input[type="text"],.adminForm textarea{
    width:100%;
    padding: 8px;
    border:1px solid #ddd;
    background: #fafafa;
    border-radius: 4px;
    margin-bottom: 16px;
    margin-top: 4px;
    font-size: 16px;
    box-sizing: border-box;
    outline: none;
}
.adminForm img{
    height: 100px;
    padding: 12px;
}
.adminForm textarea{
    height: 200px;
}
.adminForm button.saveButton{
    padding: 12px;
    width: 100%;
    border-radius: 4px;
    border: none;
    color: #fff;
    background: #187dec;
}
h1.adminHeader{
    text-align: left;
}
ul.adminListOfPosts{
    text-align: left;
    margin:0px;
    padding: 0px;
}
ul.adminListOfPosts li{
    list-style: none;
    margin: 16px 0;
}
ul.adminListOfPosts li a{
    text-decoration: none;
    color: #333;
}
.newPostLink{
    text-decoration: none;
    text-align: left;
    display: block;
    text-align: left;
}
.newPostButton{
    background: #187dec;
    display: inline-block;
    color: #fff;
    margin: 12px 0px;
    padding: 12px;
}

